<template>
  <v-container fluid>
    <v-row align="center" justify="center">
      <v-col class="d-flex" cols="12" sm="3">
        <v-select
          dense
          @change="dataChange"
          v-model="selected.category"
          :items="items.category"
          item-text="text"
          item-value="value"
          label="Kategorie"
          outlined
        ></v-select>
      </v-col>
      <v-col class="d-flex" cols="12" sm="3">
        <v-select
          dense
          @change="dataChange('kraj')"
          v-model="selected.region"
          :items="items.region"
          item-text="text"
          item-value="value"
          label="Kraj"
          outlined
        ></v-select>
      </v-col>
      <v-col class="d-flex" cols="12" sm="3">
        <v-select
          dense
          @change="dataChange"
          v-model="selected.district"
          :items="items.district"
          item-text="text"
          item-value="value"
          label="Okres"
          outlined
        ></v-select>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios';
import qs from 'qs';

export default {
  name: 'selectBar',
  mounted() {
    this.fetchData();
  },
  computed: {
    okresy() {
      const all = Object.keys(this.okresyKraju).reduce((acc, val) => {
        acc.push(...this.okresyKraju[val]);
        return acc;
      }, []);
      all.sort((a, b) => a.localeCompare(b));
      all.unshift('Všechny okresy');
      return this.selected.kraj === 'Všechny kraje' ? all : this.okresyKraju[this.selected.kraj];
    },
    kraje() {
      const all = Object.keys(this.okresyKraju);
      all.sort((a, b) => a.localeCompare(b));
      all.unshift('Všechny kraje');
      return all;
    },
    sortedCats() {
      const sortedCats = [...this.categories].sort((a, b) => a.localeCompare(b));
      sortedCats.unshift('Všechny kategorie');
      return sortedCats;
    },
  },
  methods: {
    dataChange(target) {
      if (target === 'kraj') {
        this.selected.district = 'all';
      }
      this.$emit('change', this.selected);
      this.fetchData();
    },
    fetchData() {
      const baseUrl = process.env.VUE_APP_BASE_URL;
      const queryString = qs.stringify(this.selected);
      console.log('idu request');
      axios.get(`${baseUrl}/test?${queryString}`).then(({ data }) => {
        this.items = data;
      });
    },
  },
  data: () => ({
    selected: {
      region: 'all',
      district: 'all',
      category: 'all',
    },
    items: {
      region: [],
      district: [],
      category: [],
    },
