      'Ostraha objektů a majetku',
      'Odvoz komunálního odpadu',
      'Revize kotlů',
      'Úklid bytových prostor',
      'Revize výtahů a eskalátorů',
      'Servis klimatizací',
      'Servisy tepelné techniky',
      'Stěhování firem a kanceláří',
    ],
    okresyKraju: {
      'Jihomoravský kraj': [
        'Blansko',
        'Brno-město',
        'Brno-venkov',
        'Břeclav',
        'Hodonín',
        'Vyškov',
