<template>
  <v-app>
    <app-header />
    <v-container fluid>
      <select-bar @change="filtersChanged($event)" />
      <v-data-table
        dense
        :headers="headers"
        :items="suppliers"
        :items-per-page="-1"
        class="elevation-1"
        item-key="_id"
        hide-default-footer
        show-expand
        :single-expand="true"
        :expanded.sync="expanded"
      >
        <template v-slot:expanded-item="{ headers, item }">
          <td :colspan="headers.length">
            <p class="expanded-row">{{ item.description }}</p>
          </td>
        </template>
      </v-data-table>
      <div class="pagination-container">
        <v-btn class="pagination-btn" @click="previousPage" :disabled="query.skip === 0"
          >Předchozí</v-btn
        >
        <div class="supplier-count">
          ({{ query.skip + 1 }} -
          <!-- eslint-disable-next-line vue/no-parsing-error -->
          {{ query.skip + query.limit < totalCount ? query.skip + query.limit : totalCount }}) /
          {{ totalCount }}
        </div>
        <v-btn
          class="pagination-btn"
          @click="nextPage"
          :disabled="query.skip + query.limit > totalCount"
          >Další</v-btn
        >
      </div>
    </v-container>
  </v-app>
</template>

<script>
import axios from 'axios';
import qs from 'qs';
import selectBar from './components/selectBar.vue';
import AppHeader from './components/appHeader.vue';

// import AppHeader from './components/appHeader.vue';

export default {
  name: 'App',

  components: {
    selectBar,
    AppHeader,
    // AppHeader
  },
  mounted() {
    this.fetchSuppliers(this.query);
  },
  methods: {
    fetchSuppliers(query) {
      const baseUrl = process.env.VUE_APP_BASE_URL;
      // const url = Object.keys(query).length ? `${baseUrl}?${qs.stringify(query)}` : baseUrl;
      const url = `${baseUrl}?${qs.stringify(query)}`;
      console.log({ url });
      axios.get(url).then(({ data }) => {
        console.log({ data });
        this.suppliers = data.data;
        this.totalCount = data.count;
      });
    },
    filtersChanged(filterQuery) {
      this.query = { skip: 0, limit: 15, ...filterQuery };
      this.fetchSuppliers(this.query);
    },
    nextPage() {
      this.query.skip += this.query.limit;
      this.fetchSuppliers(this.query);
    },
    previousPage() {
      if (this.query.skip > 0) {
        this.query.skip -= this.query.limit;
        this.fetchSuppliers(this.query);
      }
    },
  },

  data: () => ({
    totalCount: 0,
    query: { skip: 0, limit: 15 },
    expanded: [],
    headers: [
      {
        text: 'Název dodavatele',
        value: 'title',
      },
      {
        text: 'Telefon',
        sortable: false,
        value: 'phone',
      },
      {
        text: 'E-mail',
        sortable: false,
        value: 'email',
      },
      {
        text: 'Adresa',
        sortable: false,
        value: 'address',
      },
      { text: '', value: 'data-table-expand' },
    ],
    suppliers: [],
  }),
};
</script>

